@import "./classNameVar";
//  className  mixin

// "z-"
@mixin z($name, $global: false) {
	$all: $--prefix + $name;
	:global {
		.#{$all} {
			@content;
		}
	}
}
// "exist-"
@mixin exist($name) {
	@at-root {
		&.#{$--exist + $name} {
			@content;
		}
	}
}
// "is-"
@mixin is($name) {
	@at-root {
		&.#{$--is + $name} {
			@content;
		}
	}
}
