/* create by zgt */
@import "../../scssMixin/generateClassName2";
@import "../vars";
// key:value  
$--label-color: $heading-color;
//z-info

@include z("info", $global: true) {
	position: relative;
	$border-color: $--light-blue;
	$bg-color: lighten($--light-blue, 5%);
	display: flex;
	border-top: 1px solid $border-color;
	border-bottom: 1px solid $border-color;
	margin: 0;
	min-height: 100%;
	@include is("border-right") {
		border-right: 1px solid $border-color;
	}
	@at-root {
		$--p: 10px;
		@include z("info-left", $global: true) {
			position: relative;
			color: $--label-color;
			margin: 0;
			width: 160px;
			padding: $--p 16px 0px;
			display: flex;
			align-items: center;
			align-self: auto;
			// border-left: 1px solid $border-color;
			border-right: 1px solid $border-color;
			background-color: $bg-color;
		}
		//z-info-right
		@include z("info-right", $global: true) {
			position: relative;
			margin: 0;
			padding: $--p 16px 0px;
			flex: 1;
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			align-self: auto;
			white-space: normal;
			word-break: break-all;
		}
		@include z("span", $global: true) {
			margin-bottom: $--p;
		}
	}
}
:global {
	.z-info + .z-info {
		margin-top: -1px;
	}
}
